<template>
    <div class="secure">
        <van-nav-bar
        title="个人资料"
        left-text="返回"
        left-arrow
        @click-left="black"
        />
        <BgBox>
            <van-cell-group>
                <van-cell title="修改密码" is-link @click="popup"/>
                <van-popup v-model="show"  closeable close-icon="close" position="bottom" :style="{ height: '30%' }">
                    <van-field
                    v-model="password.oldPassword"
                    name="旧密码"
                    label="旧密码"
                    placeholder="6-16位密码"
                    autocomplete="off"
                    />
                    <van-field
                    v-model="password.newPassword"
                    name="新密码"
                    label="新密码"
                    placeholder="6-16位密码"
                    autocomplete="off"
                    />
                    <van-button type="primary" round color="linear-gradient(to right, #ff6034, #ee0a24)" size="large" @click="updatePassword">确认修改</van-button>
                </van-popup>
                <van-cell title="注销账号" is-link @click="Dialog"/>
            
            </van-cell-group>
        </BgBox>
       
        <van-button type="primary" round color="linear-gradient(to right, #ff6034, #ee0a24)" size="large" @click="exitLogin">退出登录</van-button>
    </div> 
</template>

<script>
    import { verifyForm } from '../assets/js/verifyForm'
    import BgBox from "../components/BgBox.vue"
    export default {
        name: 'Secure',
        components: {
            BgBox
        },
        data() {
            return {
                show: false,
                password: {
                    oldPassword: '',
                    newPassword: ''
                }   
            }
        },
        methods: {
            black(){
                this.$router.go(-1);
            },
            popup(){
                this.show = !this.show;
            },
            //修改密码
            updatePassword(){
                let o = {
                    oldPassword: {
                        value: this.password.oldPassword,
                        errorMsg: "旧密码由数字字母下划线组合(6-16字符)",
                        reg: /^[A-Za-z]\w{5,15}$/,
                    },
                    newPassword: {
                        value: this.password.newPassword,
                        errorMsg: "新密码由数字字母下划线组合(6-16字符)",
                        reg: /^[A-Za-z]\w{5,15}$/,
                    },
                };
                let isPass = verifyForm.verify(o);
                if(isPass){
                    if(this.password.oldPassword == this.password.newPassword){
                        this.$toast("新密码与旧密码不能相同");
                        return;
                    }
                }

                let tokenString = localStorage.getItem("token");

                if (!tokenString) {
                    this.$toast("请先登录");
                    return this.$router.push({ name: "Login" });
                }

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                });
                this.axios({
                    method: 'POST',
                    url: '/updatePassword',
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        password: this.password.newPassword,
                        oldPassword: this.password.oldPassword
                    }
                }).then(res => {
                    this.$toast.clear();
                   
                    if (result.data.code == 700) {
                    //token检验无效,则跳到登录页面
                    this.$router.push({ name: "Login" });
                    } else if (result.data.code == "E001") {
                    setTimeout(() => {
                        //清除登录状态
                        localStorage.removeItem("token");
                        this.$router.push({ name: "Login" });
                    }, 500);
                    }
                    this.$toast(res.data.msg);
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            //注销
            Dialog(){
                this.$dialog.confirm({
                title: '注销账号',
                message: '是否确认注销账号，一旦确认无法恢复！',
                theme: 'round-button',
                }).then(() => {
                     let tokenString = localStorage.getItem("token");

                    if (!tokenString) {
                        this.$toast("请先登录");
                        return this.$router.push({ name: "Login" });
                    }

                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0,
                    });
                    this.axios({
                        method: 'POST',
                        url: '/destroyAccount',
                        data: {
                            appkey: this.appkey,
                            tokenString,
                        }
                    }).then(res => {
                        this.$toast.clear();
                        if(res.data.code == 700){
                            this.$router.push({name: 'Login'});
                        }else if(res.data.code == "G001"){
                            setTimeout(() => {
                                localStorage.removeItem("token");
                                this.$router.push({name: 'Login'});
                            },500)
                        }
                        this.$toast(res.data.msg);
                    }).catch(err => {
                        this.$toast.clear();
                    })

                }).catch(() => {

                });
            },
            //退出登录
            exitLogin(){
                  let tokenString = localStorage.getItem("token");

                    if (!tokenString) {
                        this.$toast("请先登录");
                        return this.$router.push({ name: "Login" });
                    }

                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0,
                    });
                    this.axios({
                        method: 'POST',
                        url: '/logout',
                        data: {
                            appkey: this.appkey,
                            tokenString,
                        }
                    }).then(res => {
                        this.$toast.clear();
                        if(res.data.code == 700){
                            this.$router.push({name: 'Login'});
                        }else if(res.data.code == "F001"){
                            setTimeout(() => {
                                localStorage.removeItem("token");
                                this.$router.push({name: 'Login'});
                            },500)
                        }
                        
                        this.$toast(res.data.msg);
                    }).catch(err => {
                        this.$toast.clear();
                    })
            }
        },
       
    }
</script>

<style lang="less" scoped>
    .secure{
        .van-button--large{
            position: fixed;
            bottom: 20px;
            left: 50%;
            width: 95%;
            margin-left: -179px;
        }
    }
</style>